.toaster {
   position: fixed;
   z-index: 99999999;
   opacity: .90;
   width: 280px;
   right: 10px;
   margin-bottom: 10px;
   box-shadow: 1px 3px 4px rgba(0, 0, 0, .4);
   border: 1px solid #000;
   border-radius: 4px;
   padding: 6px 12px;
   bacground: #eee;
   cursor: default;
   clip-path: inset(0 0 100% 0);

   .icon {
      font-size: 36px;
      width: 58px;
      left: -6px;
      position: relative;
      display: table-cell;
      vertical-align: middle;
   }

   .body {
      color: #000;
      padding-top: 0;
      line-height: 1.3;
      display: table-cell;
      vertical-align: middle;
   }

}